@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
    margin: 0px;
    padding: 0px;
    background-color: #03030373;
    font-family: 'Lato', sans-serif;
  
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.container {
    margin: 0 auto;
    width: 90%;
}
ul {
    list-style: none;
}
a {
    text-decoration: none;
}
.btn {
    text-decoration: none;
    background-color: #0ac6fc;
    color: white;
    padding: 10px;
    border-radius: 20px;
    z-index: 2;
    outline: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
   
  }
  .col {
    margin-top: 50px;
  }
  .btn:hover {
    background-color: transparent;
    border: 1px solid white;
  }
#main {
    width: 100%;
    height: 700px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(/PortfolioR/static/media/world.7b920a83.jpg);
    position: relative;
    z-index: 0;
}
#main::before {
    position: absolute;
    content: "";
    background: radial-gradient(center, ellipse farthest-corner, rgba(181,8,8, 0.38) 0%, rgba(0, 0,0, 0.6) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#61b50808",endColorstr='#99000000',GradientType=1);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}
nav {
    position: fixed;
    width: 100%;
    z-index: 30;
    background-color: #01546b;
}
.h1 {
    font-size: 27px;
    color: white;
}
.h2 {
    font-size: 17px;
    color: white;
}
 ul li {
    font-family: 'lato ';
    position: relative;
    height: 40px;
    line-height: 43px;
    margin: 3px;
    padding: 0px 22px;
    display: flex;
    justify-content: center;
    cursor: pointer;
    font-size: 0.9rem;
    text-transform: uppercase;
}
li:hover {
    color: red;
}
.menu {
    color: #ffffff;
}

.menu-btn{
    position: fixed;
    display: flex;
    z-index: 100;
    right: 14px;
    top: 10px;
    flex-direction: column;
    width: 40px;
    height: 40px;
    cursor: pointer;
}
#i {
    width: 40px;
    height: 2px;
    background-color: white;
    border-radius: 5px;
    margin: 3px;
    box-shadow: 0 2px 5px rgba(255, 101, 47, .2);
    transition: all ease 0.4s;
}
.open:nth-child(1){
transform: rotate(45deg) translateY(12px);
}
.open:nth-child(2){
    opacity: 0;
}
.open:nth-child(3){
    transform: rotate(-45deg) translateY(-12px);
}
.close:nth-child(1){
    transform: rotate(0deg) translateY(0);
}
.close:nth-child(2){
    opacity: 1;
}
.close:nth-child(3){
    transform: rotate(0deg) translateY(0);
}
.menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 40px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .menu.activate {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

.headerhead {
   text-align: center;
   /* padding-top: 340px; */
   position: relative;
   top: 35%;   
}
#about {
    color: #ffffff;
    font-size: 16px;
    padding: 20px;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(/PortfolioR/static/media/abt.761752ba.png);
    position: relative;
   
}
h1 {
    text-align: center;
    padding-top: 20px;
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
}
p {
    font-size: 14px;
    padding-top: 10px;
    text-align: center;
    color: white;
    margin-bottom: 14px;
}
#portfolio {
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(/PortfolioR/static/media/port.fd8d7426.jpg);
    position: relative;
    z-index: 0;
    color: white;
    padding: 20px;
}
img {
    max-width: 100%;
}
#contacts {
    background-color: #ffffff;
    padding: 20px;
}
.conta {
    position: relative;
    z-index: 3;
    margin: 0 auto;
    width: 50%;
    margin-bottom: 50px;
    border-radius: 10px;

}
.port-card {
    position: relative;
  }
  
  .port-card img {
    width: 100%;
    display: block;
  }
  .port-card .inner-card-controls {
    display: inline;
    position: absolute;
    bottom: 50%;
    left: 50%;
    transform: translateX(-50%);
    /* background: rgba(0, 0, 0, 0.8); */
    border-radius: 5px;
    padding: 3px;
    opacity: 0;
    transition: all 0.3s ease;
    
  }
  
  .port-card:hover .inner-card-controls {
    opacity: 1;
  }
  .btall {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    
  }
  .bt {
    text-decoration: none;
   background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 10px;
    z-index: 2;
    word-spacing: 8px;
    outline: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border: none;
    cursor: pointer;
  }
.tat {
    color: black;
}
.conts {
    text-align: center;
    margin-bottom: 20px;
}
.all-below {
    text-align: center;
}
.below {
    margin-left: 2px;
}
.fb {
    background-color: #347689;
    padding: 12px;
    color: white;
  }
  .goo {
    background-color: #347689;
    padding: 12px;
    color: white;
  }
  .twi {
    background-color: #347689;
    padding: 12px;
    color: white;
  }
  .git{
    background-color: #347689;
    padding: 12px;
    color: white;
  }
  .li {
    background-color: #347689;
    padding: 12px;
    color: white;
  }
  .fb:hover {
    background-color: #3b5998;
    transition: background-color .9s ease-in-out;
  }
  .goo:hover {
    background-color: #db4a39;
    transition: background-color .9s ease-in-out;
  }
  .twi:hover {
    background-color: #00acee;
    transition: background-color .9s ease-in-out;
  }
  .net:hover {
    background-color: #4e658e;
    transition: background-color .9s ease-in-out;
  }
  .li:hover {
    background-color: #00acee;
    transition: background-color .9s ease-in-out;
  }

@media(min-width:600px){
    #main {
        height: 700px;
    }
    nav {
        width: 100%;
        z-index: 30;
        background-color: #01546b;
        transition: 0.6s ease-in-out;
    }
    .menu-btn{
        display: none;
    }
    .menu{  
        background: #01546b;
        left: 0;
        top: 0;
        transition: all 0.5s ease;
        z-index: 1;
        display: grid;
        grid-template-columns: repeat(4, auto);
        grid-gap: 10px;
        list-style: none;
        text-align: center;
        width: 100%;
        justify-content: center;
        margin-right: 2rem;
        position: fixed;
      }
    .conta1, .conta2, .conta3{
        display: flex;
        grid-gap: 40px;
        gap: 40px;
        position: relative;
        z-index: 3;
        margin: 0 auto;
        width: 50%;
        margin-bottom: 50px;
        border-radius: 10px;
    
    }
}


